.footer{
  background-color:black;
    height:500px;
    position: sticky;
    top:0;
    display: flex;
    flex-direction: row;
    column-gap: 100px;
    align-items:center;
    justify-content: center;
}

.footer-2{
    display: flex;
    flex-direction: row;
    column-gap: 60px;
}

.footer-text-1{
    color:white;
    font-size: 20px;
}

.footer-text-2{
    color:white;
    font-size: 14px;
}

.footer-text-3{
    color:#ff691f;
    font-size: 14px;
}

.footer-text-4{
    color:#ff691f;
    font-size: 14px;
}

.footer-text-5{
    color:white;
    font-size: 12px;
}

@media (max-width: 478px) {

    .footer{
        background-color: black;
        height:500px;
        position: sticky;
        top:0;
        display: flex;
        flex-direction: row;
        column-gap: 30px;
        align-items:center;
        justify-content: center;
        padding-left:30px;
    }
    .footer-2{
        display: flex;
        flex-direction: row;
        column-gap: 30px;
    }

    .footer-text-1{
        color:white;
        font-size: 14px;
    }
}.footer-dark {
    padding:50px 0;
    color:#f0f9ff;
    background-color:black;
  }
  
  .footer-dark h3 {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    font-size:16px;
  }
  
  .footer-dark ul {
    padding:0;
    list-style:none;
    line-height:1.6;
    font-size:14px;
    margin-bottom:0;
  }
  
  .footer-dark ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.6;
  }
  
  .footer-dark ul a:hover {
    opacity:0.8;
  }
  
  @media (max-width:767px) {
    .footer-dark .item:not(.social) {
      text-align:center;
      padding-bottom:20px;
    }
  }
  
  .footer-dark .item.text {
    margin-bottom:36px;
  }
  
  @media (max-width:767px) {
    .footer-dark .item.text {
      margin-bottom:0;
    }
  }
  
  .footer-dark .item.text p {
    opacity:0.6;
    margin-bottom:0;
  }
  
  .footer-dark .item.social {
    text-align:center;
  }
  
  @media (max-width:991px) {
    .footer-dark .item.social {
      text-align:center;
      margin-top:20px;
    }
  }
  
  .footer-dark .item.social > a {
    font-size:20px;
    width:36px;
    height:36px;
    line-height:36px;
    display:inline-block;
    text-align:center;
    border-radius:50%;
    box-shadow:0 0 0 1px rgba(255,255,255,0.4);
    margin:0 8px;
    color:#fff;
    opacity:0.75;
  }
  
  .footer-dark .item.social > a:hover {
    opacity:0.9;
  }
  
  .footer-dark .copyright {
    text-align:center;
    padding-top:24px;
    opacity:0.3;
    font-size:13px;
    margin-bottom:0;
  }