.hero-container{
    background:url('./1.png') ;

    height: 800px;
   
    object-fit: cover;

    display: flex;
    align-items: center;
    justify-content: center;
      background-position: center top;
}


.inner-hero{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    color:white;
    z-index: 99 !important;
   
}

  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
  }

  
  .btn--outline:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  